
import { useState } from 'react';
import './App.css';

function App() {

  let time = new Date().toLocaleTimeString();

  const [curtime, setCtime] = useState(time);

  const upTime = () => {
    time = new Date().toLocaleTimeString();
    setCtime(time);
  };
  setInterval(upTime, 1000)
  return (
    <>
      <div>{curtime}</div>
    </>
  );
}

export default App;
